.container{
    display: flex;
    margin: 28px 0 95px 96px;
    flex-direction: column;
    width: 100%;
    max-width: 880px;

}
.itemsContainer{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.itemsContainer div{
    width: 100%;

}
.title{
    font-size: 32px;
    font-weight: 600;
    line-height: 130%;
    text-align: start;
}
.items{
    background: #FFFFFF;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 24px 32px;
    margin-right: 20px;
    margin-top: 24px;
    min-width: 100%;
    min-height: 90px;
    box-sizing: border-box;
    text-align: start;
}
.items a{
    text-decoration: none;
    margin-bottom: 16px;
}
.user {
    margin-bottom: 42px;
}
.disabled div.arrow{
    display: none;
    margin: 0 5px;
}
.disabled .disabledArrow{
    display: block;
    margin: 0 5px;
}
.disabledArrow{
    display: none;
    margin: 0 5px;
}
.pagination {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    padding: 0px;
    list-style-type:none;
    position: relative;
    width: 200px;
    height: 25px;
    bottom: 3px;
}
.paginationContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    justify-content: flex-end;

}
.paginationContainer > div{
    margin-right: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #808080;
    min-width: 150px;
}

.description {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}
.page{
    width: 25px;
    height: 21px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #808080;
    margin-left: 10px;

}
.page:hover{cursor: pointer}
.active {
    background: #0064EB;
    color: white;
    border-radius: 3px;
    width: 21px;
    height: 25px;
}
.text{
    height: 62px;
    font-weight:400;
    font-size: 22px;
    line-height: 30.8px;
    text-align: center;
    color: #808080;
}


@media all and (max-width: 960px){
    .items{
        padding: 15px 16px;
        margin-top: 22px;
    }
    .description {
        font-size: 14px;
        line-height: 15px;
    }
    .title{
        font-size: 28px;
    }
}
@media all and (max-width: 720px){
    .container{
        margin: 20px 0 70px 20px;
        width: 92%;
    }
}

@media all and (max-width: 416px){

    .paginationContainer > div{
        margin-right: 5px;
        min-width: 100px;
    }
    .page{
        width: 20px;
        height: 17px;
    }
    .pagination {
        width: 158px;

    }
}