.container{
    max-width: 290px;
    max-height: 430px;
    margin-top: 39px;
    padding-left: 56px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-align: left;
    box-sizing: border-box;
}
.avatar{
    width: 280px;
    height: 280px;
    border-radius: 50%;
    margin-bottom: 20px;
}
.name {
    font-weight: 600;
    font-size: 26px;
    line-height: 33.8px;
    margin-bottom: 12px;
}
.login {
    font-weight: 400;
    font-size: 18px;
    line-height: 21.78px;
    margin-bottom: 26px;
}
.login a{
    color: #0064EB;
    text-decoration:none;
}
.followInfo{
    position: relative;
    display: flex;
    justify-content: space-between;
}
.followInfo span{

}
.followInfo img{
    position: relative;
    top: 6px;
    margin-right: 8px;
    width: 24px;
    height: 24px;
}
@media all and (max-width: 960px){
    .container{
        padding-left: 40px;
    }
    .avatar{
        width: 250px;
        height: 250px;
        margin-bottom: 18px;
    }
    .name {
        font-size: 22px;
    }
    .login {
        font-size: 14px;
        margin-bottom: 15px;
    }
}
@media all and (max-width: 720px){
    .container{
        padding-left: 0px;
    }
}


